*
  outline: none

html, body, #root
  height: 100%

html
  font-size: 16px
  scroll-behavior: smooth

body
  margin: 0 auto
  font-family: "Montserrat", sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  font-weight: 500

  #root
    display: flex
    flex-direction: column

    .main
      width: 100%
      margin: auto
      flex-grow: 1
      display: flex
      flex-direction: column

a
  text-decoration: none
p
  line-height: 1.5

ul
  list-style: none
  padding: 0
  margin: 0

@media (max-width:1023px)
  html
    font-size: 12px
