.aboutMe
    display: flex
    flex-direction: column
    align-items: center
    gap: 30px

    .about__content, .about__competences
        max-width: 1440px
        width: 60%
        display: flex

    .about__content
        flex-direction: row
        align-items: center
        margin: auto
        justify-content: space-between
    img
        width: 150px
        height: 150px
        margin-right: 50px

    p
        text-align: justify

    .about__competences
        flex-direction: column
        align-items: center

        >h3
            font-size: 2rem

        &__icones
            display: flex
            justify-content: flex-start
            gap: 20px
            font-size: 4rem

@media (max-width:768px)
    .aboutMe
        .about__content
            width: 80%

            img
                width: 100px
                height: 100px

@media (max-width:575px)
    .aboutMe
        .about__content
            flex-direction: column

            img
                margin: 20px 0

        .about__competences
            width: 80%
            align-items: flex-start

            &__icones
                gap: 15px
                font-size: 3rem
