.home__main
    background: rgb(214, 64, 69)
    background: linear-gradient(90deg, rgba(70,117,153,1) 0%, rgba(214,64,69,1) 100%)
    margin: 0

    &__section
        margin: 0
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        max
        > h2
            font-size: 3rem
            text-align: center

        &.works, &.contact, &.about
            padding: 50px 0

        &.works, &.contact
            background-color: #f2f2f2
            > p
                text-align: justify
                line-height: 1.5
                font-size: 1rem

        &.works
            > h2, > p
                max-width: 1440px
                width: 80%
                color: #467599

            .worksList
                max-width: 1440px
                width: 80%
                display: grid
                grid-template-columns: 1fr 1fr
                justify-content: space-evenly
                grid-gap: 100px
                margin: 50px 0

        &.about
            display: flex
            flex-direction: column
            align-items: center

        &.contact
            >h2
                color: #D64045

@media (max-width:768px)
    .home__main
        &__section
            &.works

                .worksList
                    width: 90%
                    padding: 20px
                    grid-gap: 30px
                    grid-template-columns: 1fr
